// MOBILE L: FROM 576 TO 768
@media screen and (min-width: $mobile-l) and (max-width: $to-tablet) {
	.portfolio {
		main .columns .column {
			.categories p {
				font-size: rem(16px); } } } }

// TABLET: UP TO 1023
@media screen and (max-width: $to-desktop-s) {
	header .navbar .navbar-menu {
		.navbar-end {
			position: fixed;
			width: 50%;
			height: 100%;
			top: 0;
			right: -50%;
			text-align: center;
			transition: all .5s;
			button {
				display: block; }
			.background {
				position: fixed;
				left: 0;
				top: 0;
				z-index: -1;
				width: 100vw;
				height: 100vh;
				background-color: transparentize($black, .25);
				opacity: 0;
				transition: all .5s; }
			.closeMenu {
				position: absolute;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				z-index: 5;
				height: 20px;
				width: 20px;
				top: 20px;
				left: 30px;
				&:hover {
					cursor: pointer;
					span:nth-child(1) {
						transform: rotate(135deg); }
					span:nth-child(2) {
						transform: rotate(45deg); } }
				span {
					position: absolute;
					height: 3px;
					width: 100%;
					background-color: $black;
					transition: all .2s;
					&:nth-child(1) {
						transform: rotate(45deg); }
					&:nth-child(2) {
						transform: rotate(-45deg); } } }
			&.mobileMenuOpen {
				right: 0;
				.closeMenu {
					@include animation (closeButtonIn, 2s, 1, ease);
					@include animation-fill-mode (forwards); }
				.background {
					opacity: 1; } } }
		ul {
			position: fixed;
			top: 0;
			background-color: $white;
			height: 100%;
			width: inherit;
			margin: 0;
			flex-direction: column;
			justify-content: flex-end;
			align-items: flex-start;
			padding: rem(20px) rem(60px);
			font-size: rem(28px);
			box-shadow: rem(-4px) 0 rem(50px) transparentize($black, .75);
			transition: all .5s; }
		li {
			font-weight: normal;
			color: transparentize($black, .5);
			margin: 0;
			margin-bottom: rem(20px);
			&:last-child {
				margin-bottom: 0; }
			.current-page {
				color: $black;
				font-weight: 700;
				border-bottom: 0;
				&:after {
					content: '';
					position: absolute;
					left: rem(-40px);
					top: 50%;
					transform: translateY(-50%);
					background-color: $black;
					height: rem(3px);
					width: rem(20px); } }
			&:hover {
				color: $black; } } }
	.home {
		.about {
			& > .columns {
				flex-direction: column; }
			.column {
				width: 100%;
				&:nth-child(1) p:last-child {
					margin-bottom: 0; } }
			.card {
				margin-top: 50px;
				transform: translateY(50px); } }
		.action {
			h1 {
				font-size: 80px;
				line-height: 90px; } } }
	.portfolio {
		main {
			.columns .column .categories {
				height: calc(100vw / 100 * 80 / 2 - 10px); } }
		.modal {
			.presentation .content {
				display: none; } } }

	.contact {
		main {
			form {
				& * {
					width: 80%; } } }
		// .waves
		// 	.wave1
		// 		background-image: url("../src/contact/contact-tablet-wave1.svg")
		// 	.wave2
 } }		// 		background-image: url("../src/contact/contact-tablet-wave2.svg")

// TABLET: FROM TO 768
@media screen and (min-width: $tablet) {
	.no-desktop {
		display: none !important; }

	.portfolio {
		main {
			.filterButtons p:nth-child(2) {
				display: none; } } } }

