// MOBILE: UP TO 767
@media screen and (max-width: $to-tablet) {
	header .navbar .navbar-menu {
		.navbar-end {
			position: fixed;
			width: 75%;
			height: 100%;
			top: 0;
			right: -75%;
			text-align: center;
			transition: all .5s; } }
	.home {
		h2 {
			font-size: 50px;
			line-height: 60px; }
		.welcome {
			p {
				font-size: 30px;
				text-align: left;
				span {
					font-size: inherit; } } }
		.services {
			h2 {
				margin-bottom: 50px; } }
		.action {
			h1 {
				font-size: 60px;
				line-height: 70px; } } }
	.portfolio {
		.modal .presentation .background {
			z-index: 5; } } }

// MOBILE: UP TO 768
@media screen and (max-width: $tablet) {
	.no-mobile {
		display: none !important; }
	main {
		h1 {
			padding-top: calc(50px + 80px);
			margin-bottom: 50px; } }
	.portfolio {
		main {
			.columns .column {
				margin-bottom: 10px;
				.categories {
					height: calc(100vw / 100 * 80 / 1 - 10px); } }
			.filterButtons p:nth-child(1) {
				display: none; } }
		.modal {
			align-items: flex-end;
			height: 100%;
			.presentation {
				display: block;
				width: 100%;
				.content {
					display: none; } }
			aside {
				position: absolute;
				top: auto;
				width: 100%;
				max-height: 75%;
				border-radius: rem(50px) rem(50px) 0 0;
				padding-top: rem(50px);
				padding-bottom: rem(50px);
				.close {
					height: rem(5px);
					width: rem(100px);
					background-color: #c4c4c4;
					border-radius: rem(10px);
					position: absolute;
					top: rem(10px);
					left: 50%;
					transform: translateX(-50%);
					img {
						display: none; } }
				h2 {
					margin-top: rem(40px); }
				&.modalSidebar-modalOpen {
					animation-name: mobileModalOpen; }
				&.modalSidebar-modalClose {
					animation-name: mobileModalClose; } } }
		// .waves
		// 	div
		// 		&.wave1
		// 			background-image: url("../src/img/services-mobile-wave1.svg")
		// 		&.wave2
		// 			background-image: url("../src/img/services-mobile-wave2.svg")
		// 		&.wave3
 } }		// 			background-image: url("../src/img/services-mobile-wave3.svg")

// SMALL DEVICES: UP TO 576
@media screen and (max-width: $small) {
	.home {
		.about {
			.column {
				h2,p {
					width: 100%; } } }
		.services {
			.cards {
				p {
					font-size: 18px;
					line-height: 20px; }
				&>div {
					margin-right: 0; } } }
		.action {
			.to-contact a {
				width: 100%;
				padding: 15px; } } }
	.portfolio {
		main {
			.filters {
				display: grid;
				grid-template-columns: 1fr 1fr;
				grid-column-gap: 5px;
				grid-row-gap: 5px;
				div {
					&:hover span {
						transform: translateY(0);
						box-shadow: 0px 2px 4px transparentize($black, .6); }
					span {
						margin: 0;
						padding: 15px 15px; } } } } }

	.contact {
		main {
			form {
				& * {
					width: 100%; }
				input[type="submit"] {
					width: 100%; } } }
