// MOBILE-L: UP TO 425
@media screen and (max-width: $mobile-l) {
	main.container {
		max-width: 100%;
		padding: 0 20px; }
	.home {
		.hero h1 {
			font-size: 80px; }
		.action {
			h1 {
				font-size: 40px;
				line-height: 50px; } } }
	.portfolio {
		main {
			.columns .column .categories {
				height: calc(100vw - 50px);
				p {
					font-size: rem(16px); } } } } }

